.UserPage{
    z-index: 999;
}

.User .headBar {
    position: fixed;
    width: 100%;
    height: 50px;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(40, 40, 40, 0.6)), to(transparent));
    background: -webkit-linear-gradient(rgba(40, 40, 40, 0.6), transparent);
    background: -o-linear-gradient(rgba(40, 40, 40, 0.6), transparent);
    background: linear-gradient(rgba(40, 40, 40, 0.6), transparent);
    line-height: 50px;
    color: #fff;
}
.User .userHeader{
    width: 100%;
}
.User .userHeader .coverImg{
    width: 100%;
    height: 160px;
    background-position: center;
    background-size: cover;
}
.User .userHeader .head_pic {
    width: 72px;
    height: 72px;
    border-radius: 72px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 4px; 
    margin: -36px 15px 0 15px;
    background-color: #fff;
    display: inline-block;
}
.User .userHeader .head_pic img {
    width: 64px;
    height: 64px;
    border-radius: 64px;
}
.User .userHeader .countNum{
    display: block;
    font-size: 13px;
    height: 30px;
    line-height: 30px;
    margin: 5px 20px;
}
.User .userHeader .favoritesBtn {
    display: inline-block;
    border: 1px solid black;
    border-radius: 5px;
    padding: 0 10px;
    float: right;
}
.User .userHeader .tabBar span {
    display: inline-block;
    width: 110px;
    text-align: center;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
}


