html,
body,
div,
ul,
li,
a {
  padding: 0;
  margin: 0;
}
ul {
  list-style-type: none;
}
.hide {
  opacity: 0;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
  -webkit-transition: opacity 0.2s;
}
.imageview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 900;
  background-color: #000;
  overflow: hidden;
  -webkit-animation: easeshow 0.25s;
          animation: easeshow 0.25s;
}
@keyframes easeshow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes easeshow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.imagelist {
  display: -webkit-box;
  display: box;
  height: 100%;
}
.imagelist .imagelist-item {
  display: -webkit-box;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  width: 100%;
  text-align: center;
  position: relative;
  background-color: #000;
  margin-right: 40px;
}
.imagelist .imagelist-item .imagelist-item-img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
}
.page {
  font-family: -apple-system-font, 'Helvetica Neue', Helvetica, STHeiTi, sans-serif;
  position: fixed;
  font-size: 14px;
  color: #fff;
  padding: 2px 5px;
  background-color: #000;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
      transform: translateX(-50%);
}
.errorpage {
  position: absolute;
  font-size: 16px;
  text-align: center;
  color: #aaaaaa;
  top: 28%;
  left: 50%;
  margin-left: -70px;
}
.errorpage:before {
  content: '';
  display: block;
  width: 150px;
  height: 140px;
  margin: 0 auto;
  padding-bottom: 20px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100%;
  opacity: .4;
}
.spinner {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.double-bounce1,
.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #333;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}
.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@-webkit-keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}



