body, html{
  width: 100%;
  height: 100%;
}

.Index{
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 50px; 
}
.Index .head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 51px;
  background-color: #3498db;
  -webkit-box-shadow: 0 2px 4px 0 #3498db;
          box-shadow: 0 2px 4px 0 #3498db;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  z-index: 3;
}
.Index .head div {
  margin-left: 20px;
  color: #fff;
}

.Index .foot {
  position: fixed;
  left: 0;
  width: 100%;
  height: 51px;
  background-color: rgb(240, 240, 240);
  -webkit-box-shadow: 3px 0px 4px 0 rgba(167, 135, 135, 0.7);
          box-shadow: 3px 0px 4px 0 rgba(167, 135, 135, 0.7);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: 3;
  pointer-events: auto;
}

.Index .foot div {
  width: 30%;
  text-align: center;
}

.qiuqiu {
  position: fixed;
  bottom: 75px;
  right: 25px;
  width: 65px;
  height: 65px;
  border-radius: 65px;
  background-color: #3498db;
  -webkit-box-shadow: 0px 6px 30px 0px rgba(167, 135, 135, 0.5);
          box-shadow: 0px 6px 30px 0px rgba(167, 135, 135, 0.5);
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  z-index: 2;
}



